	module.exports = {"data":{"talkJson":{"description":"Lambda Days 2015 - Norbert Wójtowicz - ClojureScript + React.js: How I learned to stop worrying and love the browser\n\nNorbert Wójtowicz \nSoftware Developer at Base CRM\n\nNorbert Wójtowicz is a developer at Base CRM, whose magic 8-ball predicts that Clojure and ClojureScript will be in your future toolbox. You can find his code and ramblings in various corners of the internet, under the felicitous handle @pithyless.\n\nClojureScript + React.js: How I learned to stop worrying and love the browser.\n\nOver the last few years, there has been a stronger emphasis on functional programming languages and constructs in mainstream programming. The rise of FP popularity is not due to concurrency (although still valid), but the recognition that minimizing side-effects yields to smaller code that is easier to test and reason about. ClojureScript finally brings that perspective to the front-end world.\n\nThis talk is an introduction to a different kind of front-end development. Together, we will explore how ClojureScript helps accelerate front-end development and how to avoid the most common pitfalls; a molotov cocktail of information and pizazz to put you on the fast-track towards web development nirvana.","meta":{"duration":"PT52M24S"},"presenter":{"name":null},"slug":"lambda-days-2015-norbert-wojtowicz-clojurescript-reactjs","source":{"id":"ey7Ms2ub7wY","provider":"youtube"},"tag":["Lambda Days","Norbert Wójtowicz","ClojureScript","React.js"],"title":"Lambda Days 2015 - Norbert Wójtowicz - ClojureScript + React.js (...)"},"allTalkJson":null},"pathContext":{"slug":"lambda-days-2015-norbert-wojtowicz-clojurescript-reactjs","tags":["Lambda Days","Norbert Wójtowicz","ClojureScript","React.js"]}}

